(function() {
    'use strict';

    angular.module('MinkaMiBanco.theme')
        .run(themeRun);

    /** @ngInject */
    function themeRun($timeout, $rootScope, $state, PermRoleStore, Authorization, layoutPaths, preloader, $q, baSidebarService, themeLayoutSettings, $window, localStorageService) {

        var role = localStorageService.get('id_rol_usuario');

        if (role == null) {

            $window.location = 'auth.html';
        }


        $rootScope.$on('$stateChangeSuccess', function(event, toState, toParams, fromState, fromParams) {


            // Define anonymous role
            PermRoleStore.defineRole(role, function() {
                // If the returned value is *truthy* then the user has the role, otherwise they don't

                return true;
            });


        })

        var whatToWait = [
            preloader.loadAmCharts(),
            $timeout(2000)
        ];

        var theme = themeLayoutSettings;
        if (theme.blur) {
            if (theme.mobile) {
                whatToWait.unshift(preloader.loadImg(layoutPaths.images.root + 'blur-bg-mobile.jpg'));
            } else {
                whatToWait.unshift(preloader.loadImg(layoutPaths.images.root + 'blur-bg.jpg'));
                whatToWait.unshift(preloader.loadImg(layoutPaths.images.root + 'blur-bg-blurred.jpg'));
            }
        }

        $q.all(whatToWait).then(function() {
            $rootScope.$pageFinishedLoading = true;
        });

        $timeout(function() {
            if (!$rootScope.$pageFinishedLoading) {
                $rootScope.$pageFinishedLoading = true;
            }
        }, 5000);

        $rootScope.$baSidebarService = baSidebarService;
    }

})();
