(function () {
  'use strict';

  angular.module('MinkaMiBanco.theme')
    .service('themeLayoutSettings', themeLayoutSettings);

  /** @ngInject */
  function themeLayoutSettings(baConfig) {
    var isMobile = (/android|webos|iphone|ipad|ipod|blackberry|windows phone/).test(navigator.userAgent.toLowerCase());
    var mobileClass = isMobile ? 'mobile' : '';
    var blurClass = baConfig.theme.blur ? 'blur-theme' : '';
    angular.element(document.body).addClass(mobileClass).addClass(blurClass);

    return {
      blur: baConfig.theme.blur,
      mobile: isMobile,
    }
  }

})();
 (function() {
     'use strict';

     angular.module('login')
         .controller('LoginCtrl', LoginCtrl);

     /** @ngInject */
     function LoginCtrl($scope, usuarioService, $state, localStorageService) {

         $scope.usuarioRequest = { id_usuario: null, no_login: null, no_password: null, nombre_apellido: null };
         $scope.usuario = [];
         $scope.message = null;
         $scope.loginUsuario = function() {
             usuarioService.loginUsuario().post({}, $scope.usuarioRequest, function(result) {
                 $scope.usuario = result.usuario;
                 if ($scope.usuario.length > 0) {
                     for (var i = 0; i < $scope.usuario.length; i++) {
                         $scope.usuarioRequest.id_usuario = $scope.usuario[i].id_usuario;
                         $scope.usuarioRequest.no_login = $scope.usuario[i].no_login;
                         $scope.usuarioRequest.nombre_apellido = $scope.usuario[i].no_nombre + ' ' + $scope.usuario[i].no_apellido;
                     }
                     localStorageService.set('id_usuario', $scope.usuarioRequest.id_usuario);
                     localStorageService.set('nombre_apellido', $scope.usuarioRequest.nombre_apellido);
                     $state.go('inicio');
                 } else {
                     $scope.message = "Usuario o contraseña incorrectos"
                 }

             }, function(error) {
                 console.error(error);
             })
         };
         $scope.closeAlert = function() {
             $scope.message = null;
         };
     }

 })();
