                     $scope.usuarioRequest.no_email = $scope.usuario[i].no_email;
                     $scope.usuarioRequest.id_area = $scope.usuario[i].id_area;
                     $scope.usuarioRequest.id_cargo = $scope.usuario[i].id_cargo;

                     $scope.usuarioRequest.no_piso = $scope.usuario[i].no_piso;
