(function() {
    'use strict';
    angular.module('solicitud')
        .controller('CostoCtrl', CostoCtrl);
    /** @ngInject */
    function CostoCtrl($scope, idSolicitud,Excel, costoService, $timeout,areaService, materialesService, $uibModalInstance, toastr, viewConfig) {


        $scope.modal = { titleNuevo: "COSTO" }
        $scope.costoRequest = { id_solicitud: idSolicitud, id_costo: null, id_area: null, id_material: null, nu_cantidad: null, va_precio: null };

        $scope.valuesCosto={canti:null,prec:null};
        /*array para llenar los select*/
        $scope.costo = [];
        $scope.area = [];
        $scope.materiales = [];
        /**
         * [listarCosto description]
         * @return {[type]} [description]
         */
        $scope.listarCosto = function() {
            $scope.costoRequest.id_solicitud = idSolicitud;
            costoService.listarCostoPorSolicitud().post({}, $scope.costoRequest, function(result) {
                $scope.costo = result.costo;
                $scope.valuesCosto.prec=null;
                for (var i = 0; i < $scope.costo.length; i++) {
                    $scope.valuesCosto.prec +=($scope.costo[i].nu_cantidad * $scope.costo[i].va_precio);
                }
            }, function(error) {
                console.log(error);
            })

        };
        $scope.listarCosto();
        /**
         * [listaMaterialesPorArea]
         * @return {[type]}
         */
        $scope.listaMaterialesPorArea = function() {

            materialesService.listarMaterialesPorArea().post({}, $scope.costoRequest, function(result) {
                $scope.materiales = result.materiales;
            }, function(error) {
                console.log(error);
            })
        };
        /**
         * [listaArea]
         * @return {[type]}
         */
        $scope.listaArea = function() {
            areaService.listarArea().post({}, $scope.costoRequest, function(result) {
                $scope.area = result.area;
            }, function(error) {
                console.log(error);
            })

        };
        $scope.listaArea();
        /**
         * [insertaCosto description]
         * @return {[type]} [description]
         */
        $scope.insertaCosto = function() {
            $scope.costoRequest.id_solicitud = idSolicitud;
            costoService.insertarCosto().post({}, $scope.costoRequest, function() {
                $scope.listarCosto();
                $scope.costoRequest = { id_costo: null, id_area: null, id_material: null, nu_cantidad: null, va_precio: null };
                toastr.success("Costo Agregado", 'Costo', viewConfig.toastConfig);
            }, function(error) {
                console.error(error);
            })
        };
        /**
         * [actualizaCosto description]
         * @return {[type]} [description]
         */
        $scope.actualizaCosto = function() {
            costoService.actualizarCosto().post({}, $scope.costoRequest, function() {
                $scope.listarCosto();
                $scope.costoRequest = { id_costo: null, id_area: null, id_material: null, nu_cantidad: null, va_precio: null };
                toastr.success("Costo Actualizado", 'Costo', viewConfig.toastConfig);
            }, function(error) {
                console.error(error);
            })
        };
        /**
         * [eliminaCosto description]
         * @param  {[type]} idCosto [description]
         * @return {[type]}         [description]
         */
        $scope.eliminaCosto = function(idCosto) {
            $scope.costoRequest.id_costo = idCosto;
            costoService.eliminarCosto().post({}, $scope.costoRequest, function() {
                $scope.listarCosto();
                toastr.success("Costo Eliminado", 'Costo', viewConfig.toastConfig);
            }, function(error) {
                console.error(error);
            })
        };
        $scope.obtenEditar = function(idCosto, idSolicitud, idArea, idMaterial, cantidad, precio) {
            $scope.costoRequest.id_costo = idCosto;
            $scope.costoRequest.id_solicitud = idSolicitud;
            $scope.costoRequest.id_area = idArea;
            $scope.listaMaterialesPorArea();
            $scope.costoRequest.id_material = idMaterial;
            $scope.costoRequest.nu_cantidad = parseInt(cantidad);
            $scope.costoRequest.va_precio = parseInt(precio);
        }
        $scope.exportToExcel = function(tableId) { // ex: '#my-table'
             var exportHref = Excel.tableToExcel(tableId, 'WireWorkbenchDataExport');
             $timeout(function() { location.href = exportHref; }, 100); // trigger download
         }
        /**
         * [ok para cerrar el modal ejecutandose la funcion eliminar]
         * @return {[type]} [description]
         */
        $scope.ok = function() {
            $uibModalInstance.close();
        };
        /**
         * [cancel cancela la eliminacion del usuario]
         * @return {[type]} [description]
         */
        $scope.cancel = function() {
            $uibModalInstance.dismiss('cancel');
        };
    }
})();
